"use client";

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useUser } from '@/context/UserContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Separator } from '@/components/ui/separator';

interface PendingStudent {
  id: string;
  name: string;
  email: string;
  role: string;
  approved: boolean;
}

export default function AdminDashboard() {
  const { user, logout, loading: userLoading } = useUser();
  const [pendingStudents, setPendingStudents] = useState<PendingStudent[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const router = useRouter();

  // Form states
  const [instructorForm, setInstructorForm] = useState({
    name: '',
    email: '',
    password: '',
  });

  const [courseForm, setCourseForm] = useState({
    title: '',
    description: '',
    duration: '',
    fee: '',
    schedule: '',
    modules: '',
    outline: '',
    lectureContent: '',
  });

  useEffect(() => {
    if (!userLoading && (!user || user.role !== 'admin')) {
      router.push('/login');
      return;
    }

    // Fetch pending student registrations
    const fetchPendingStudents = async () => {
      try {
        const response = await fetch('/api/auth/register');
        if (response.ok) {
          const data = await response.json();
          setPendingStudents(data.pendingStudents || []);
        }
      } catch (err) {
        setError('Error loading pending registrations');
      } finally {
        setLoading(false);
      }
    };

    if (user && user.role === 'admin') {
      fetchPendingStudents();
    }
  }, [user, userLoading, router]);

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  const handleApproveStudent = async (studentId: string) => {
    try {
      const response = await fetch('/api/admin/approve-student', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ studentId }),
      });

      if (response.ok) {
        setPendingStudents(prev => prev.filter(student => student.id !== studentId));
        setSuccess('Student approved successfully');
        setTimeout(() => setSuccess(''), 3000);
      } else {
        setError('Failed to approve student');
      }
    } catch (err) {
      setError('Error approving student');
    }
  };

  const handleRejectStudent = async (studentId: string) => {
    try {
      const response = await fetch('/api/admin/reject-student', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ studentId }),
      });

      if (response.ok) {
        setPendingStudents(prev => prev.filter(student => student.id !== studentId));
        setSuccess('Student registration rejected');
        setTimeout(() => setSuccess(''), 3000);
      } else {
        setError('Failed to reject student');
      }
    } catch (err) {
      setError('Error rejecting student');
    }
  };

  const handleRegisterInstructor = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    try {
      const response = await fetch('/api/admin/register-instructor', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(instructorForm),
      });

      if (response.ok) {
        setSuccess('Instructor registered successfully');
        setInstructorForm({ name: '', email: '', password: '' });
        setTimeout(() => setSuccess(''), 3000);
      } else {
        const data = await response.json();
        setError(data.error || 'Failed to register instructor');
      }
    } catch (err) {
      setError('Error registering instructor');
    }
  };

  const handleUploadCourse = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    try {
      const modules = courseForm.modules.split('\n').filter(m => m.trim());
      
      const response = await fetch('/api/admin/upload-course', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...courseForm,
          modules,
        }),
      });

      if (response.ok) {
        setSuccess('Course uploaded successfully');
        setCourseForm({
          title: '',
          description: '',
          duration: '',
          fee: '',
          schedule: '',
          modules: '',
          outline: '',
          lectureContent: '',
        });
        setTimeout(() => setSuccess(''), 3000);
      } else {
        const data = await response.json();
        setError(data.error || 'Failed to upload course');
      }
    } catch (err) {
      setError('Error uploading course');
    }
  };

  if (userLoading || loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <p className="text-gray-600">Loading admin dashboard...</p>
        </div>
      </div>
    );
  }

  if (!user || user.role !== 'admin') {
    return null; // Will redirect to login
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-black text-white py-4">
        <div className="container mx-auto px-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold">The Fourth Sealing Hub</h1>
            <p className="text-gray-300">Admin Dashboard</p>
          </div>
          <div className="flex items-center space-x-4">
            <span className="text-sm">Welcome, {user.name}</span>
            <Button 
              onClick={handleLogout}
              variant="outline" 
              className="border-white text-white hover:bg-white hover:text-black"
            >
              Logout
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {/* Welcome Section */}
        <div className="mb-8">
          <h2 className="text-3xl font-bold text-black mb-2">Admin Dashboard</h2>
          <p className="text-gray-600">
            Manage student registrations, instructors, and course content.
          </p>
        </div>

        {/* Alerts */}
        {error && (
          <Alert className="border-red-200 bg-red-50 mb-6">
            <AlertDescription className="text-red-800">
              {error}
            </AlertDescription>
          </Alert>
        )}

        {success && (
          <Alert className="border-green-200 bg-green-50 mb-6">
            <AlertDescription className="text-green-800">
              {success}
            </AlertDescription>
          </Alert>
        )}

        {/* Admin Tabs */}
        <Tabs defaultValue="approvals" className="w-full">
          <TabsList className="grid w-full grid-cols-3 mb-6">
            <TabsTrigger value="approvals">Student Approvals</TabsTrigger>
            <TabsTrigger value="instructors">Register Instructors</TabsTrigger>
            <TabsTrigger value="courses">Manage Courses</TabsTrigger>
          </TabsList>

          {/* Student Approvals Tab */}
          <TabsContent value="approvals">
            <Card className="border-2 border-gray-200">
              <CardHeader>
                <CardTitle className="text-2xl text-black">Pending Student Registrations</CardTitle>
                <CardDescription>
                  Review and approve student registration requests
                </CardDescription>
              </CardHeader>
              <CardContent>
                {pendingStudents.length === 0 ? (
                  <div className="text-center py-8">
                    <p className="text-gray-600">No pending student registrations</p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {pendingStudents.map((student) => (
                      <div key={student.id} className="border border-gray-200 rounded-lg p-4">
                        <div className="flex justify-between items-start">
                          <div>
                            <h3 className="font-semibold text-black">{student.name}</h3>
                            <p className="text-gray-600">{student.email}</p>
                            <p className="text-sm text-gray-500 capitalize">Role: {student.role}</p>
                          </div>
                          <div className="flex space-x-2">
                            <Button
                              onClick={() => handleApproveStudent(student.id)}
                              className="bg-green-600 text-white hover:bg-green-700"
                            >
                              Approve
                            </Button>
                            <Button
                              onClick={() => handleRejectStudent(student.id)}
                              variant="outline"
                              className="border-red-600 text-red-600 hover:bg-red-600 hover:text-white"
                            >
                              Reject
                            </Button>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          {/* Register Instructors Tab */}
          <TabsContent value="instructors">
            <Card className="border-2 border-gray-200">
              <CardHeader>
                <CardTitle className="text-2xl text-black">Register New Instructor</CardTitle>
                <CardDescription>
                  Add new instructors to the platform
                </CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleRegisterInstructor} className="space-y-4">
                  <div>
                    <Label htmlFor="instructor-name" className="text-black font-medium">
                      Full Name
                    </Label>
                    <Input
                      id="instructor-name"
                      type="text"
                      value={instructorForm.name}
                      onChange={(e) => setInstructorForm(prev => ({ ...prev, name: e.target.value }))}
                      required
                      className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                      placeholder="Enter instructor's full name"
                    />
                  </div>

                  <div>
                    <Label htmlFor="instructor-email" className="text-black font-medium">
                      Email Address
                    </Label>
                    <Input
                      id="instructor-email"
                      type="email"
                      value={instructorForm.email}
                      onChange={(e) => setInstructorForm(prev => ({ ...prev, email: e.target.value }))}
                      required
                      className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                      placeholder="Enter instructor's email"
                    />
                  </div>

                  <div>
                    <Label htmlFor="instructor-password" className="text-black font-medium">
                      Password
                    </Label>
                    <Input
                      id="instructor-password"
                      type="password"
                      value={instructorForm.password}
                      onChange={(e) => setInstructorForm(prev => ({ ...prev, password: e.target.value }))}
                      required
                      className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                      placeholder="Enter password for instructor"
                    />
                  </div>

                  <Button
                    type="submit"
                    className="w-full bg-black text-white hover:bg-gray-800"
                  >
                    Register Instructor
                  </Button>
                </form>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Manage Courses Tab */}
          <TabsContent value="courses">
            <Card className="border-2 border-gray-200">
              <CardHeader>
                <CardTitle className="text-2xl text-black">Upload New Course</CardTitle>
                <CardDescription>
                  Add new courses and update course materials
                </CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleUploadCourse} className="space-y-4">
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="course-title" className="text-black font-medium">
                        Course Title
                      </Label>
                      <Input
                        id="course-title"
                        type="text"
                        value={courseForm.title}
                        onChange={(e) => setCourseForm(prev => ({ ...prev, title: e.target.value }))}
                        required
                        className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                        placeholder="Enter course title"
                      />
                    </div>

                    <div>
                      <Label htmlFor="course-duration" className="text-black font-medium">
                        Duration
                      </Label>
                      <Input
                        id="course-duration"
                        type="text"
                        value={courseForm.duration}
                        onChange={(e) => setCourseForm(prev => ({ ...prev, duration: e.target.value }))}
                        required
                        className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                        placeholder="e.g., 6 months"
                      />
                    </div>
                  </div>

                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="course-fee" className="text-black font-medium">
                        Fee
                      </Label>
                      <Input
                        id="course-fee"
                        type="text"
                        value={courseForm.fee}
                        onChange={(e) => setCourseForm(prev => ({ ...prev, fee: e.target.value }))}
                        required
                        className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                        placeholder="e.g., $2,500"
                      />
                    </div>

                    <div>
                      <Label htmlFor="course-schedule" className="text-black font-medium">
                        Schedule
                      </Label>
                      <Input
                        id="course-schedule"
                        type="text"
                        value={courseForm.schedule}
                        onChange={(e) => setCourseForm(prev => ({ ...prev, schedule: e.target.value }))}
                        required
                        className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                        placeholder="e.g., Mon, Wed, Fri - 6:00 PM to 8:00 PM"
                      />
                    </div>
                  </div>

                  <div>
                    <Label htmlFor="course-description" className="text-black font-medium">
                      Description
                    </Label>
                    <Textarea
                      id="course-description"
                      value={courseForm.description}
                      onChange={(e) => setCourseForm(prev => ({ ...prev, description: e.target.value }))}
                      required
                      className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                      placeholder="Enter course description"
                      rows={3}
                    />
                  </div>

                  <div>
                    <Label htmlFor="course-modules" className="text-black font-medium">
                      Course Modules (one per line)
                    </Label>
                    <Textarea
                      id="course-modules"
                      value={courseForm.modules}
                      onChange={(e) => setCourseForm(prev => ({ ...prev, modules: e.target.value }))}
                      required
                      className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                      placeholder="Enter each module on a new line"
                      rows={6}
                    />
                  </div>

                  <div>
                    <Label htmlFor="course-outline" className="text-black font-medium">
                      Course Outline
                    </Label>
                    <Textarea
                      id="course-outline"
                      value={courseForm.outline}
                      onChange={(e) => setCourseForm(prev => ({ ...prev, outline: e.target.value }))}
                      required
                      className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                      placeholder="Enter detailed course outline"
                      rows={8}
                    />
                  </div>

                  <div>
                    <Label htmlFor="lecture-content" className="text-black font-medium">
                      First Lecture Content
                    </Label>
                    <Textarea
                      id="lecture-content"
                      value={courseForm.lectureContent}
                      onChange={(e) => setCourseForm(prev => ({ ...prev, lectureContent: e.target.value }))}
                      required
                      className="mt-1 border-gray-300 focus:border-black focus:ring-black"
                      placeholder="Enter the content for the first lecture"
                      rows={10}
                    />
                  </div>

                  <Button
                    type="submit"
                    className="w-full bg-black text-white hover:bg-gray-800"
                  >
                    Upload Course
                  </Button>
                </form>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </main>
    </div>
  );
}
